import { request } from "@/utils/request";
import { REQUEST_SUCCEEDED_CODE } from '@/common/constants';
/**
 * myBox 我的盒子界面
 * @description 我的盒子界面
 * @Author: 穆兰
 * @Date: 2022/1/10
 * @LastEditors: 穆兰
 * @LastEditTime: 2022/1/10
 */
export default {
    data() {
        return {
            tabSelected: 0,
            tookBoxInfos: [],
            tookBoxPage: 1,
            tookBoxSum: 0,
            tookBoxDetail: {},
            putBoxInfos: [],
            putBoxPage: 1,
            putBoxSum: 0,
            putBoxDetail: {}
        };
    },
    onLoad() {
        this.tookBoxLog(1);
        this.putBoxLog(1);
    },
    methods: {
        /**
         *@description 点击返回按钮
         */
        onNarLeftClick() {
            console.log("onNarLeftClick()");
            uni.navigateBack({
                delta: 1
            });
        },
        /**
         * @description 标签页改变时
         * @param {number} tab
         */
        onTabChange(tab) {
            this.tabSelected = tab;
        },
        /**
         * @description 我开的盒子页数改变时
         */
        tookBoxPageChange(e) {
            this.tookBoxPage = e.current;
            this.tookBoxLog(this.tookBoxPage);
        },
        /**
         * @description 我放的盒子页数改变时
         */
        putBoxPageChange(e) {
            this.putBoxPage = e.current;
            this.putBoxLog(this.putBoxPage);
        },
        /**
         * @description 点击某个我获取的盒子
         * @param {TookBoxInfo} item
         */
        onTookBoxItemClick(item) {
            this.tookBoxDetail = item;
            this.$refs.tookBoxDetailPopup.open();
        },
        /**
         * @description 点击某个我放入的盒子
         * @param {PutBoxInfo} item
         */
        onPutBoxItemClick(item) {
            this.putBoxDetail = item;
            this.$refs.putBoxDetailPopup.open();
        },
        /**
         * @description 点击删除按钮
         * @param {PutBoxInfo} item
         */
        onDeleteButtonClick(item) {
            let _this = this;
            uni.showModal({
                title: '删除盒子',
                content: '你确定要删除这个盒子吗？已经被收取的盒子无法删除哦',
                success: function (res) {
                    if (res.confirm) {
                        _this.deleteBox(item.id);
                        _this.$refs.putBoxDetailPopup.close();
                    }
                    else if (res.cancel) {
                        console.log('用户点击取消');
                    }
                }
            });
        },
        /**
         * @description 盲盒收取记录
         * @param {number} page 页号
         */
        async tookBoxLog(page) {
            let res = await request({
                data: {
                    method: 'GET',
                    group: 'blindBox',
                    action: 'collect/log',
                    data: {
                        page: page
                    },
                    header: {
                        'content-type': 'application/x-www-form-urlencoded',
                    },
                }
            });
            if (res.data.code === REQUEST_SUCCEEDED_CODE) {
                this.tookBoxInfos = res.data.data.list;
                this.tookBoxSum = res.data.data.total;
            }
            else {
                console.log('tookBoxLogFail', res.data);
            }
        },
        /**
         * @description 盲盒投递记录
         * @param {number} page 页号
         */
        async putBoxLog(page) {
            let res = await request({
                data: {
                    method: 'GET',
                    group: 'blindBox',
                    action: 'deliver/log',
                    data: {
                        page: page
                    },
                    header: {
                        'content-type': 'application/x-www-form-urlencoded',
                    },
                }
            });
            if (res.data.code === REQUEST_SUCCEEDED_CODE) {
                this.putBoxInfos = res.data.data.list;
                this.putBoxSum = res.data.data.total;
            }
            else {
                console.log('putBoxLogFail', res.data);
            }
        },
        /**
         * @description 删除指定id的盲盒
         * @param {number} id 盲盒id
         */
        async deleteBox(id) {
            let res = await request({
                data: {
                    method: 'DELETE',
                    group: 'blindBox',
                    action: `${id}/delete`,
                    data: {
                        id: id
                    },
                    header: {
                        'content-type': 'application/x-www-form-urlencoded',
                    },
                }
            });
            if (res.data.code === REQUEST_SUCCEEDED_CODE) {
                console.log('删除成功');
                for (let i = 0; i < this.putBoxInfos.length; i++) {
                    if (this.putBoxInfos[i].id == id) {
                        this.putBoxInfos[i].isDeleted = 1;
                        break;
                    }
                }
            }
            else {
                console.log('tookBoxLogFail', res.data);
            }
        }
    }
};
